const PACKAGE_NAME = '@wix/auto_sdk_data_sharing';

/**
 * Creates a data sharing policy for the specified collection.
 *
 * You can optionally filter which items in the collection can be shared. If no filters are specified, all items in the collection are shared. You can create multiple policies for the same collection with different filters.
 *
 * You can find which fields can be filtered for each collection by calling [Get Data Collection](https://dev.wix.com/docs/api-reference/business-solutions/cms/data-collections/get-data-collection). The supported filters are returned in the response under `collections.fields.capabilities.queryOperators`.
 *
 * <blockquote class="important">
 *
 * __Important:__
 * Only collections created in the CMS or with the [Data Collections API](https://dev.wix.com/docs/api-reference/business-solutions/cms/introduction) can be shared. External collections and Wix App collections can't be shared.
 *
 * </blockquote>
 */
