export const __metadata = { PACKAGE_NAME: '@wix/data' };

export function createDataSharingPolicy(
  httpClient: HttpClient
): CreateDataSharingPolicySignature {
  return (
    options?: NonNullablePaths<
      CreateDataSharingPolicyOptions,
      `dataSharingPolicy.dataCollectionId`,
      3
    >
  ) =>
    universalCreateDataSharingPolicy(
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface CreateDataSharingPolicySignature {
  /**
   * Creates a data sharing policy for the specified collection.
   *
   * You can optionally filter which items in the collection can be shared. If no filters are specified, all items in the collection are shared. You can create multiple policies for the same collection with different filters.
   *
   * You can find which fields can be filtered for each collection by calling [Get Data Collection](https://dev.wix.com/docs/api-reference/business-solutions/cms/data-collections/get-data-collection). The supported filters are returned in the response under `collections.fields.capabilities.queryOperators`.
   *
   * <blockquote class="important">
   *
   * __Important:__
   * Only collections created in the CMS or with the [Data Collections API](https://dev.wix.com/docs/api-reference/business-solutions/cms/introduction) can be shared. External collections and Wix App collections can't be shared.
   *
   * </blockquote>
   * @returns Created data sharing policy.
   */
  (
    options?: NonNullablePaths<
      CreateDataSharingPolicyOptions,
      `dataSharingPolicy.dataCollectionId`,
      3
    >
  ): Promise<
    NonNullablePaths<DataSharingPolicy, `_id` | `dataCollectionId`, 2> & {
      __applicationErrorsType?: CreateDataSharingPolicyApplicationErrors;
    }
  >;
}

export function updateDataSharingPolicy(
  httpClient: HttpClient
): UpdateDataSharingPolicySignature {
  return (_id: string, dataSharingPolicy: UpdateDataSharingPolicy) =>
    universalUpdateDataSharingPolicy(
      _id,
      dataSharingPolicy,
      // @ts-ignore
      { httpClient }
    );
}

interface UpdateDataSharingPolicySignature {
  /**
   * Updates an existing data sharing policy.
   *
   * You can only update the `dataItemsFilter` field to change which items from the collection are shared.
   * You can't change which collection is shared. If you want to share a different collection, create a new data sharing policy
   *
   * Any updates to the policy automatically apply to all sites connected using this policy.
   * @param - Data sharing policy ID.
   * @returns Updated data sharing policy.
   */
  (_id: string, dataSharingPolicy: UpdateDataSharingPolicy): Promise<
    NonNullablePaths<DataSharingPolicy, `_id` | `dataCollectionId`, 2> & {
      __applicationErrorsType?: UpdateDataSharingPolicyApplicationErrors;
    }
  >;
}

export function deleteDataSharingPolicy(
  httpClient: HttpClient
): DeleteDataSharingPolicySignature {
  return (dataSharingPolicyId: string) =>
    universalDeleteDataSharingPolicy(
      dataSharingPolicyId,
      // @ts-ignore
      { httpClient }
    );
}

interface DeleteDataSharingPolicySignature {
  /**
   * Deletes a data sharing policy and disconnects all associated connections.
   *
   * <blockquote class="warning">
   *
   * __Warning:__
   * When you delete a sharing policy, all target sites connected to the shared collection using this policy immediately lose access to the collection and its data. This can cause code in the target sites to break.
   *
   * </blockquote>
   * @param - Data sharing policy ID.
   */
  (dataSharingPolicyId: string): Promise<
    void & {
      __applicationErrorsType?: DeleteDataSharingPolicyApplicationErrors;
    }
  >;
}

